'use client';

import { useState } from 'react';
import { Network, Cpu, Database, Cloud, Smartphone, Users } from 'lucide-react';

const ServiceEcosystem = () => {
  const [activeNode, setActiveNode] = useState<string | null>(null);
  
  const nodes = [
    {
      id: 'iot',
      title: 'IoT & Capteurs',
      description: 'Collecte de données en temps réel',
      icon: <Cpu className="w-8 h-8" />,
      position: 'top-1/2 left-1/4',
    },
    {
      id: 'data',
      title: 'Traitement Data',
      description: 'Analyse et transformation des données',
      icon: <Database className="w-8 h-8" />,
      position: 'top-1/3 left-1/2',
    },
    {
      id: 'ia',
      title: 'Intelligence Artificielle',
      description: 'Algorithmes prédictifs et automatisation',
      icon: <Network className="w-8 h-8" />,
      position: 'top-2/3 left-1/2',
    },
    {
      id: 'cloud',
      title: 'Cloud & Infrastructure',
      description: 'Hébergement scalable et sécurisé',
      icon: <Cloud className="w-8 h-8" />,
      position: 'top-1/2 right-1/4',
    },
    {
      id: 'apps',
      title: 'Applications',
      description: 'Web, mobile et interfaces utilisateur',
      icon: <Smartphone className="w-8 h-8" />,
      position: 'bottom-10 left-1/3',
    },
    {
      id: 'users',
      title: 'Utilisateurs',
      description: 'Expérience et valeur business',
      icon: <Users className="w-8 h-8" />,
      position: 'bottom-10 right-1/3',
    }
  ];
  
  const connections = [
    { from: 'iot', to: 'data' },
    { from: 'data', to: 'ia' },
    { from: 'ia', to: 'cloud' },
    { from: 'cloud', to: 'apps' },
    { from: 'apps', to: 'users' },
    { from: 'data', to: 'apps' },
    { from: 'ia', to: 'users' }
  ];

  return (
    <section className="py-20">
      <div className="container px-4 mx-auto">
        <div className="text-center mb-12">
          <h2 className="text-3xl font-bold text-black mb-4">
            Notre écosystème technologique intégré
          </h2>
          <p className="text-gray-700 max-w-2xl mx-auto">
            Toutes nos solutions sont interconnectées pour offrir une expérience complète et cohérente
          </p>
        </div>
        
        {/* Interactive Ecosystem Visualization */}
        <div className="relative h-[600px] bg-white rounded-3xl border border-gray-200 overflow-hidden">
          {/* Connections */}
          <svg className="absolute inset-0 w-full h-full">
            {connections.map((conn, index) => {
              const fromNode = nodes.find(n => n.id === conn.from);
              const toNode = nodes.find(n => n.id === conn.to);
              
              if (!fromNode || !toNode) return null;
              
              return (
                <line
                  key={index}
                  x1="25%"
                  y1="50%"
                  x2="50%"
                  y2="33%"
                  stroke={activeNode === conn.from || activeNode === conn.to ? '#e38f00' : '#d1d5db'}
                  strokeWidth="2"
                  className="transition-colors duration-300"
                />
              );
            })}
          </svg>
          
          {/* Nodes */}
          {nodes.map((node) => (
            <div
              key={node.id}
              className={`absolute ${node.position} transform -translate-x-1/2 -translate-y-1/2`}
              onMouseEnter={() => setActiveNode(node.id)}
              onMouseLeave={() => setActiveNode(null)}
            >
              <div className={`
                relative w-24 h-24 rounded-full border-4 
                border-[#e38f00]
                ${activeNode === node.id ? 'scale-110' : ''}
                transition-all duration-300
                flex items-center justify-center
                bg-[#e38f00]
                cursor-pointer group
              `}>
                <div className="text-white">
                  {node.icon}
                </div>
                
                {/* Tooltip */}
                <div className="absolute -bottom-24 left-1/2 transform -translate-x-1/2 w-64 p-4 bg-white rounded-lg shadow-xl opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-300 z-10 border border-gray-200">
                  <h4 className="font-bold text-black mb-1">{node.title}</h4>
                  <p className="text-sm text-gray-700">{node.description}</p>
                  <div className="absolute -top-2 left-1/2 transform -translate-x-1/2 w-4 h-4 bg-white rotate-45 border-l border-t border-gray-200" />
                </div>
              </div>
            </div>
          ))}
          
          {/* Central Hub */}
          <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2">
            <div className="relative">
              <div className="w-32 h-32 rounded-full bg-gradient-to-r from-[#e38f00] to-[#d48500] border-8 border-white shadow-2xl flex items-center justify-center">
                <span className="text-white text-2xl font-bold">VIBECRO</span>
              </div>
              
              {/* Pulsing animation */}
              <div className="absolute inset-0 rounded-full bg-[#e38f00] animate-ping opacity-20" />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ServiceEcosystem;